<template>
    <div class="mainc">
        <div class="personal-content">
            <div class="address-content" bindtap="getUserLocation">
                <span class="address-title">:</span>
                <span class="address-text">{{userMsg.address || userMsg.area_desc || '----'}}</span>
            </div>
            <div class="shop-msg">
                <div class="logo-content" bindtap="choiceLogo">
                    <img :src="config+(userMsg.store_face||'/youxuan/icons/logo.png')" class="logo-img">
                    <div class="logo-tip"></div>
                </div>
                <div class="text-content">
                    <div class="shop-name">({{userMsg.store_sn || "--"}}){{userMsg.store_name || "--"}}</div>
                    <input type="text" bindinput="inputText" :value="signature"
                           :placeholder="inputActive?'':(userMsg.store_sign||'')"
                           class="edit-signature" :class="[inputActive?'active':'']">
                </div>
            </div>
            <div class="shop-tip1">
                <div class="tip-name"></div>
                <div class="tip1-item">
                    <span class="text">{{ordersCount ? ordersCount.order_rate : "--"}}%</span>
                </div>
                <div class="tip1-item">
                    <span class="text">{{ordersCount ? ordersCount.order_rank : "--"}}</span>
                </div>
                <div class="proportion">
                    <img class="canvas-img" src="">
                    <span class="total">:{{ordersCount ? (ordersCount.order_num > 10000 ? (ordersCount.order_num / 10000) + "" : ordersCount.order_num) : "--"}}</span>
                    <span class="my-order">:{{ordersCount ? (ordersCount.store_order_num > 10000 ? (ordersCount.store_order_num / 10000) + "" : ordersCount.store_order_num) : "--"}}</span>
                </div>
                <div class="tip1-text">
                    <span class="left"></span>
                    <span class="right">{{income ? income.today_royalty : "--"}}</span>
                </div>
                <div class="tip1-text">
                    <span class="left"></span>
                    <span class="right">{{income ? income.total_royalty : "--"}}</span>
                </div>
                <button class="ranking-btn" bindtap="divTap" data-url="../ranking/ranking"></button>
            </div>
            <div class="shop-tip2">
                <div class="btns-item">
                    <button class="item-btn" hover-class=" " open-type="share" bindtap="onShareAppMessage">
                        <i class="item-icon shop-icon1"></i>
                        <span class="item-text"></span>
                    </button>
                    <div class="item-btn" @click="backHome">
                        <i class="item-icon shop-icon2"></i>
                        <span class="item-text"></span>
                    </div>
                    <router-link tag="div" :to="{ path:'aftersale'}" class="item-btn">
                        <i class="item-icon shop-icon3"></i>
                        <span class="item-text">/</span>
                    </router-link>
                </div>
                <div class="btns-item">
                    <router-link tag="div" :to="{ path:'pushmoney'}" class="item-btn">
                        <i class="item-icon shop-icon4"></i>
                        <span class="item-text"></span>
                    </router-link>
                    <router-link tag="div" :to="{ path:'clearing'}" class="item-btn">
                        <i class="item-icon shop-icon5"></i>
                        <span class="item-text">/</span>
                    </router-link>
                    <router-link tag="div" :to="{ path:'order2'}" class="item-btn">
                        <i class="item-icon shop-icon6"></i>
                        <span class="item-text"></span>
                    </router-link>
                </div>
                <div class="btns-item">
                    <router-link tag="div" :to="{ path:'ranking'}" class="item-btn">
                        <i class="item-icon shop-icon7"></i>
                        <span class="item-text"></span>
                    </router-link>
                    <router-link tag="div" :to="{ path:'shaidan'}" class="item-btn">
                        <i class="item-icon shop-icon8"></i>
                        <span class="item-text"></span>
                    </router-link>
                    <router-link tag="div" :to="{ path:'bill'}" class="item-btn">
                        <i class="item-icon shop-icon9"></i>
                        <span class="item-text"></span>
                    </router-link>
                </div>
                <div class="btns-item">
                    <router-link tag="div" :to="{ path:'seevip'}" class="item-btn">
                        <i class="item-icon shop-icon10"></i>
                        <span class="item-text"></span>
                    </router-link>
                    <div class="item-btn">
                        <i class="item-icon shop-icon11"></i>
                        <span class="item-text"></span>
                    </div>
                    <router-link tag="div" :to="{ path:'forget',query: { type: '2' }}" class="item-btn">
                        <i class="item-icon shop-icon12"></i>
                        <span class="item-text"></span>
                    </router-link>
                </div>
            </div>
            <div class="shop-tip3">
                <img :src="config+userMsg.store_code" class="qrcode-img">
                <div class="qrcode-text"></div>
            </div>
            <button class="quit-btn" @click="logOut"></button>
        </div>

        <div class="modal-address active">
            <div class="modal-content">
                <div class="modal-text">
                    <div class="modal-title"></div>
                    <input class="address-input" placeholder="" value=""
                           bindinput="inputAddress">
                </div>
                <div class="btns-content">
                    <button class="btn-item btn-cancel" hover-class=" " bindtap="hideAddress"></button>
                    <button class="btn-item btn-confirm" hover-class=" " bindtap="confirmAddress"></button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: "personal2",
        data(){
            return {
                config: this.GLOBAL.config,
                userMsg: null,
                inputActive: false,
                income: null,
                ordersCount: null,
                signature: "",
                addressModal: 0,
                address: "",
                choiceAddress: "",
                longitude: "",
                latitude: "",
                canvasimg: ""
            }
        },
        created: function () {
            var userMsg = this.GLOBAL.userMsg;
            var signature = this.GLOBAL.userMsg.store_sign;
            this.userMsg = this.GLOBAL.userMsg;
            this.signature = this.GLOBAL.userMsg.store_sign;
            this.getshopData();
        },
        mounted() {

        },

        methods: {
            getshopData: function () {
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                //
                this.axios.get('/minstore/orders/ordersCount', {
                    params: {
                        time: 'd',
                        access_token: this.userMsg.access_token
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        this.ordersCount = datas.data;
                        var bfb = datas.data.order_num == 0 ? 0 : datas.data.store_order_num / datas.data.order_num;
//                          this.showCanvas(bfb);
                    } else {
                        this.info = this.$createToast({
                            txt: datas.error_msg,
                            type: 'txt'
                        })
                        this.info.show();
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: 'ordersCount',
                        type: 'txt'
                    })
                    this.toast.show();
                })

                //
                this.axios.get('/minstore/orders/income', {
                    params: {
                        access_token: this.userMsg.access_token
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        this.income = datas.data;
                    } else {
                        this.info = this.$createToast({
                            txt: datas.error_msg,
                            type: 'txt'
                        })
                        this.info.show();
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: 'income',
                        type: 'txt'
                    })
                    this.toast.show();
                })

            },
            backHome: function () {
                this.$router.push({name: 'index'});
            },
            logOut: function () {
                this.$createDialog({
                    type: 'confirm',
                    title: '',
                    content: '',
                    onConfirm: () => {
                        this.GLOBAL.clear();
                        localStorage.removeItem('userMsg');
                        localStorage.removeItem('role');
                        localStorage.setItem('refresh', 1);
                        this.toast = this.$createToast({
                            txt: '',
                            time: 1000,
                            type: 'txt'
                        })
                        this.toast.show();
                        setTimeout(() => {
                            this.$router.push({name: 'index'});
                        }, 1000)
                    }
                }).show()
            }
        },
        watch: {},
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_personal2.css);
</style>