<template>
    <div class="mainc">
        <div class="top-flex">
            <div class="seach-content">
                <span class="seach" @click="showAlert"></span>
            </div>
            <div class="change-list" :data-item="index">
                <div class="change-item" :class="[index==1?'active':'']" @click="changeItem" data-index="1"></div>
                <div class="change-item" :class="[index==2?'active':'']" @click="changeItem" data-index="2"></div>
            </div>
        </div>
        <div class="shou-container" ref="bscroll">
            <div class="afterSale-content bscroll-container">
                <div class="afterSale-item" v-for="(item,i) in saleList" v-if="!item.hide">
                    <div class="order-msg">
                        <div class="span">:
                            <span class="em">{{item.order_sn}}</span>
                        </div>
                        <div class="span">:
                            <span class="em">{{item.create_time}}</span>
                        </div>
                    </div>
                    <router-link tag="div" class="goods-msgs" :to="{path:'orderdet2', query: { oid: item.order_id }}">
                        <img class="goods-img" :src="config+item.banner">
                        <div class="goods-txt">
                            <span class="p">{{item.goods_name}}</span>
                            <span class="span" v-if="index==1">{{item.buy_num || 1}}</span>
                        </div>
                    </router-link>
                    <div class="client-msg">
                        <div class="span">:
                            <span class="em">{{item.username || item.replace_name}}</span>
                        </div>
                        <div class="span">:
                            <span class="em">{{item.phone || item.replace_phone}}</span>
                        </div>
                    </div>
                    <div class="status" bindtap="divTap" data-key="" v-if="index==2">
                        {{item.refund_status == 1 ? "|" : item.refund_status == 2 ? "|" : item.refund_status == 3 ? "|" : item.refund_status == 4 ? "|" : item.refund_status == 5 ? "|" : "|"}}
                    </div>
                    <div class="btns-content">
                        <button class="btn-item" @click="applySale" v-if="index==1"  :data-key="i" :data-id="item.id">
                        </button>
                        <button class="btn-item cancel" bindtap="cancelApply" v-if="index==2&&item.refund_status==1">
                            
                        </button>
                    </div>
                </div>
                <div class="btm-nomore">{{nomore == 1 ? '' : nomore == 3 ? '' : ' '}}</div>
            </div>
        </div>

        <div class="seach-container" :class="[alert==2?'active':'']" v-if="alert>0" @click="hidAlert"></div>
        <div class="sale-seach" :class="[alert==2?'active':'']" v-if="alert>0">
            <div class="h4"></div>
            <div class="condition">
                <div class="seach-title">
                    <span class="span">()</span>
                </div>
                <div class="time-seach">
                    <div class="span" data-time="sTime">


                        <div class="picker" @click="showsTime">{{sTime?sTime:""}}</div>

                    </div>
                    <span class="em"></span>
                    <div class="span" data-time="etime">

                        <div class="picker" @click="showeTime">{{eTime?eTime:""}}</div>

                    </div>
                </div>
                <div class="seach-title">
                    <span class="span">()</span>
                </div>
                <input type="text" class="seach-input" placeholder=""  v-model="salePhone">
                <div class="seach-title">
                    <span class="span">()</span>
                </div>
                <input type="text" class="seach-input" placeholder="" v-model="saleOrder">
            </div>
            <div class="seach-btns">
                <button class="seachBtn-item" @click="saleReset"></button>
                <button class="seachBtn-item" hover-class=" " @click="seachUp"></button>
            </div>
        </div>
    </div>
</template>
<script>
    import "../../assets/format"
    import BScroll from 'better-scroll'
    export default {
        name: "aftersale",
        data(){
            return {
                config: this.GLOBAL.config,
                index: 1,
                nomore: 2, //1 2 3
                page: 1,
                alert: 0,
                createTime: '2018-11-11',
                nowTime: new Date().format('yyyy-MM-dd'),
                sTime: '',
                eTime: '',
                tip1:'',
                tip2:'',
                salePhone: '',
                saleOrder: '',
                saleList: []
            }
        },
        created: function () {
            this.createTime = new Date(this.GLOBAL.shopMsg.create_time).format('yyyy-MM-dd')
            this.getData()
        },
        mounted() {
            setTimeout(() => {
                this.initScroll();
            }, 20)
        },

        methods: {
            changeItem: function (e) {
                var ind = e.target.dataset.index;
                if (ind != this.index) {
                    this.index = ind;
                    this.page = 1;
                    this.nomore = 2;
                    this.getData()
                }
            },
            getData: function () {
                var obj = this;
                if (obj.nomore != 2) {
                    return
                }
                this.nomore = 1;
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show();
                var searchs = '{"user_info":"' + obj.salePhone + '","create_time":"' + obj.sTime + '","create_time1":"' + obj.eTime + '","order_id":"' + obj.saleOrder + '"}';
                this.axios.get('/minstore/orders/searchOrderGoods', {
                    params: {
                        access_token: this.GLOBAL.userMsg.access_token,
                        search: searchs,
                        type: this.index,
                        page: this.page,
                        num: 10
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();

                    if (obj.page == 1) {
                        this.saleList = [];
                    }
                    if (datas.error_code == 0) {
                        var datalist = datas.data;
                        var list = this.saleList;
                        for (var i = 0, len = datalist.length; i < len; i++) {
                            var objs = datalist[i];
                            datalist[i].create_time = new Date(objs.create_time * 1000).format('yyyy-MM-dd hh:mm:ss');
                            if (objs.time) {
                                datalist[i].time = new Date(objs.time * 1000).format('yyyy-MM-dd hh:mm:ss');
                            } else {
                                var firstN = objs.replace_name.replace(/^(.{1}).+/ig, '$1');
                                datalist[i].replace_name = objs.replace_name.replace(/.{1}/ig, '*').replace(/^.{1}/ig, firstN);
                                datalist[i].replace_phone = objs.replace_phone.replace(/^(.{3})(.{4})(.+)/ig, '$1****$3');
                            }
                        }
                        list = list.concat(datalist);

                        this.saleList = list;
                        if (datalist.length < 10) {
                            this.nomore = 3;
                        } else {
                            this.nomore = 2;
                            this.page = this.page + 1
                        }
                    } else {
                        this.nomore = 3;
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: 'searchOrderGoods',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            initScroll(){
                if (!this.$refs.bscroll) {
                    return;
                }
                this.scroll = new BScroll(this.$refs.bscroll, {
                    click: true,
                    scrollbar: true,
                    pullUpLoad: {
                        threshold: 50
                    }
                });
                this.scroll.on('pullingUp', () => {
                    this.getData();
                })
            },
            showAlert(){
                this.alert = 1;
                setTimeout(() => {
                    this.alert = 2;
                }, 10)
            },
            //
            showsTime() {
                if (!this.Picker) {
                    this.Picker = this.$createDatePicker({
                        title: '',
                        min: new Date(2008, 7, 8),
                        max: new Date(2050, 9, 20),
                        value: new Date(),
                        format: {
                            year: 'YYYY',
                            month: 'MM',
                            date: 'D'
                        },
                        onSelect: this.selectS,
                    })
                }

                this.Picker.show()
            },
            selectS(date, selectedVal, selectedText) {
                var tip1 = new Date(date).getTime();
                this.tip1 = tip1;
                this.sTime = `${selectedText.join('-')}`;
            },
            showeTime() {
                if (!this.formatPicker) {
                    this.formatPicker = this.$createDatePicker({
                        title: '',
                        min: new Date(2008, 7, 8),
                        max: new Date(2050, 9, 20),
                        value: new Date(),
                        format: {
                            year: 'YYYY',
                            month: 'MM',
                            date: 'D'
                        },
                        onSelect: this.selectE,
                    })
                }

                this.formatPicker.show()
            },
            selectE(date, selectedVal, selectedText) {
                var tip2 = new Date(date).getTime();
                this.tip2 = tip2;
                if(tip2>=this.tip1){
                    this.eTime = `${selectedText.join('-')}`;
                }else{
                    this.eTime = this.sTime;
                }
            },
            //

            //
            saleReset(){
                this.nomore = 2;
                this.page =1;
                this.sTime = '';
                this.eTime = '';
                this.salePhone = '';
                this.saleOrder = '';
                this.tip1 = '';
                this.tip2 = '';
            },
            seachUp(){
                console.log(this.eTime, this.sTime,this.saleOrder,this.salePhone);
                this.nomore = 2;
                this.page = 1;
                this.getData();
                this.hidAlert()
            },
            //
            hidAlert: function() {
                this.alert = 1;
                setTimeout(() => {
                    this.alert = 0;
                }, 300)
            },
            //
            applySale(e){
                var keys = e.target.dataset.key;
                var gid = e.target.dataset.id;
                this.$createDialog({
                    type: 'confirm',
                    title: '',
                    content: '',
                    onConfirm: () => {
                        const pop = this.$createToast({
                            time: 10000,
                        });
                        pop.show();
                        this.axios.get('/minstore/orders/addOrderRefund', {
                            params: {
                                access_token:this.GLOBAL.userMsg.access_token,
                                id: gid
                            }
                        }).then(res => {
                            var datas = res.data;
                            pop.hide();
                            if (datas.error_code == 0) {
                                var list = this.saleList;
                                list[keys].hide = true;
                                this.saleList = list;
                                this.toast = this.$createToast({
                                    txt: '',
                                    type: 'txt'
                                })
                                this.toast.show();
                            } else {
                                this.info = this.$createToast({
                                    txt: datas.error_msg,
                                    type: 'txt'
                                })
                                this.info.show();
                            }
                        }).catch(error => {
                            this.toast = this.$createToast({
                                txt: 'addOrderRefund',
                                type: 'txt'
                            })
                            this.toast.show();
                        })
                    }
                }).show()
            }
        },
        watch: {
            saleList: {
                handler: function () {
                    this.$nextTick(() => {
                        if (this.scroll) {
                            this.scroll.finishPullUp();
                            this.scroll.refresh();
                        }
                    })
                },
                deep: true
            }
        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_aftersale.css);
</style>