<template>
    <div class="mainc">
        <div class="s_clearing-content">
            <div class="s_clearing-tip1">
                <router-link tag="div" :to="{path:'trading'}" class="s_record">></router-link>
                <img :src="imgs" class="s_img"/>
                <p> ()</p>
                <p>{{datas.can_withdraw||'0.00'}}</p>
            </div>
        </div>
        <div class="s_clearing-tip2"> =  - </div>
        <div class="s_clearing-tip3">
     	<span>
        	<i> ()</i>
            <b>{{datas.balance||'0.00'}}</b>
        </span>
            <span>
        	<i> ()</i>
            <b>{{datas.in_withdraw||'0.00'}}</b>
        </span>
        </div>
        <router-link tag="div" :to="{path:'withdraw',query:{ withdraw: datas.can_withdraw }}" class="s_apply-withdraw"></router-link>
        <div class="s_clearing-tip4">
            <router-link tag="div" :to="{path:'pushmoney'}" class="s_tip4-item"><i class="item1"></i></router-link>
            <router-link tag="div" :to="{path:'withdrawal'}" class="s_tip4-item"><i class="item2"></i></router-link>
            <router-link tag="div" :to="{path:'trading'}" class="s_tip4-item"><i class="item3"></i></router-link>
            <router-link tag="div" :to="{path:'bank'}" class="s_tip4-item"><i class="item4"></i></router-link>
        </div>
    </div>
</template>
<script>
    import "../../assets/format"
    import BScroll from 'better-scroll'
    export default {
        name: "clearing",
        data(){
            return {
                config: this.GLOBAL.config,
                imgs: this.GLOBAL.config + '/youxuan/icons/money.png',
                datas: {}
            }
        },
        created: function () {
            this.getData()
        },
        mounted() {
            var refresh =  localStorage.getItem('withdraw');// 1
            localStorage.removeItem('withdraw');
            if (refresh==1){
                this.getData()
            }
        },

        methods: {
            viewBack: function() {
               this.$router.back(-1);
            },
            getData(){
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minstore/store/storeMoneyDesc', {
                    params: {
                        access_token: this.GLOBAL.userMsg.access_token
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        this.datas = datas.data;
                    } else {
                        this.toast = this.$createToast({
                            txt: datas.error_msg,
                            type: 'txt'
                        })
                        this.toast.show();
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: 'storeMoneyDesc',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            }
        },
        watch: {

        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_clearing.css);
</style>