<template>
    <div class="mainc pushmoney">
        <div class="bar-nav">
            <button class="bar-btn left-bar-btn bar-back" hover-class=" " @click="viewBack"></button>
            <button class="bar-btn right-bar-btn" hover-class=" " @click="tapseach"></button>
            <span class="bar-title"></span>
        </div>
        <div class="ticheng-tip1">
            <div class="type-content" v-if="!isseach">
                <div class="type-item" :class="[index==1?'active':'']" @click="changeItem" data-index="1"></div>
                <div class="type-item" :class="[index==2?'active':'']" @click="changeItem" data-index="2"></div>
                <div class="type-item" :class="[index==3?'active':'']" @click="changeItem" data-index="3"></div>
                <div class="type-item" :class="[index==4?'active':'']" @click="changeItem" data-index="4"></div>
            </div>
            <div class="type-content" v-if="isseach">
                <div class="seach-contents">
                    <div class="input-span" data-time="sTime">
                        <div class="picker" @click="showsTime">{{sTime?sTime:""}}</div>
                    </div>
                    <span class="em"></span>
                    <div class="input-span" data-time="eTime">
                        <div class="picker" @click="showeTime">{{eTime?eTime:""}}</div>
                    </div>
                </div>
            </div>
            <div class="sum-money">
                <div class="span">
                    <span class="em"></span>10
                </div>
            </div>
        </div>
        <div class="shou-container" ref="bscroll">
            <div class="ticheng-content bscroll-container">
                <div class="title"></div>
                <div class="tc-item" v-for="(item,i) in list">
                    <div class="item-tip1">
                        <div class="span">:{{item.time}}</div>
                        <div class="span">{{item.goods_name}}
                            <div>{{item.standard}}</div>
                        </div>
                    </div>
                    <div class="item-tip2">
                        <div class="span"></div>
                        <div class="em">{{item.royalty-0}}</div>
                        <div class="span">/{{item.goods_unit||""}}</div>
                    </div>
                    <div class="item-tip2">
                        <div class="span"></div>
                        <div class="em">{{item.buy_num}}</div>
                    </div>
                    <div class="item-tip3">
                        <div class="span"></div>
                        <div class="em">{{item.total_royalty}}</div>
                    </div>
                </div>
                <div class="btm-nomore">{{nomore==1?'':nomore==3?'':' '}}</div>
            </div>
        </div>
    </div>
</template>
<script>
    import "../../assets/format"
    import BScroll from 'better-scroll'
    export default {
        name: "pushmoney",
        data(){
            return {
                config: this.GLOBAL.config,
                index: 1,
                nomore: 2, //1 2 3
                page: 1,
                isseach: false,
                createTime: '2018-11-11',
                nowTime: new Date().format('yyyy-MM-dd'),
                sTime: '',
                eTime: '',
                allRroyalty: 0,
                list: []
            }
        },
        created: function () {
            this.createTime = new Date(this.GLOBAL.shopMsg.create_time).format('yyyy-MM-dd')
            this.getData()
        },
        mounted() {
            setTimeout(()=>{
                this.initScroll();
            },20)
        },

        methods: {
            viewBack:function () {
              this.$router.back(-1);
            },
            changeItem: function (e) {
                var ind = e.target.dataset.index;
                if (ind != this.index) {
                    this.index = ind;
                    this.page = 1;
                    this.nomore = 2;
                    this.getData()
                }
            },
            tapseach:function () {
                this.isseach = !this.isseach;
            },
            getData: function () {
                var obj = this;
                if (obj.nomore != 2) {
                    return
                }
                this.nomore = 1;
                var searchs;
                if (this.isseach) {
                    searchs = '{"create_time":"' + this.sTime + '","create_time1":"' + this.eTime + '"}';
                } else {
                    var ind = this.index;
                    searchs = ind == 1 ? "d" : ind == 2 ? "w" : ind == 3 ? "m" : "all";
                }
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show();
                this.axios.get('/minstore/store/storeRoyaltyList', {
                    params: {
                        access_token: this.GLOBAL.userMsg.access_token,
                        search: searchs,
                        page: this.page,
                        num: 10
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();

                    if (obj.page == 1) {
                        this.list = [];
                    }
                    if (datas.error_code == 0) {
                        var list = this.list;
                        var len = datas.data.length;
                        for(var i=0;i<len;i++){
                            datas.data[i].time = new Date(datas.data[i].time * 1000).format('yyyy-MM-dd');
                        }
                        list = list.concat(datas.data);
                        this.list = list;
                        this.allRroyalty = datas.all_royalty || 0;
                        if (datas.data.length < 10) {
                            this.nomore = 3;
                        } else {
                            this.nomore = 2;
                            this.page = this.page + 1;
                        }
                    } else {
                        this.nomore = 3;
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: 'storeRoyaltyList',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },

            //
            showsTime() {
                if (!this.Picker) {
                    this.Picker = this.$createDatePicker({
                        title: '',
                        min: new Date(2008, 7, 8),
                        max: new Date(2050, 9, 20),
                        value: new Date(),
                        format: {
                            year: 'YYYY',
                            month: 'MM',
                            date: 'D'
                        },
                        onSelect: this.selectS,
                    })
                }

                this.Picker.show()
            },
            selectS(date, selectedVal, selectedText) {
                var tip1 = new Date(date).getTime();
                this.tip1 = tip1;
                this.sTime = `${selectedText.join('-')}`;
            },
            showeTime() {
                if (!this.formatPicker) {
                    this.formatPicker = this.$createDatePicker({
                        title: '',
                        min: new Date(2008, 7, 8),
                        max: new Date(2050, 9, 20),
                        value: new Date(),
                        format: {
                            year: 'YYYY',
                            month: 'MM',
                            date: 'D'
                        },
                        onSelect: this.selectE,
                    })
                }

                this.formatPicker.show()
            },
            selectE(date, selectedVal, selectedText) {
                var tip2 = new Date(date).getTime();
                this.tip2 = tip2;
                if(tip2>=this.tip1){
                    this.eTime = `${selectedText.join('-')}`;
                }else{
                    this.eTime = this.sTime;
                }
                this.getData();
            },
            //

            initScroll(){
                if(!this.$refs.bscroll){
                    return ;
                }
                this.scroll = new BScroll(this.$refs.bscroll,{
                    click:true,
                    scrollbar:true,
                    pullUpLoad: {
                        threshold: 50
                    }
                });
                this.scroll.on('pullingUp',()=>{
                    this.getData();
                })
            },
        },
        watch:{
            list:{
                handler:function(){
                    this.$nextTick(()=>{
                        if(this.scroll) {
                            this.scroll.finishPullUp();
                            this.scroll.refresh();
                        }
                    })
                },
                deep:true
            }
        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_pushmoney.css);
</style>