<template>
    <div>
    <cube-button class="times" @click="showFormatPicker">{{times}}</cube-button>
    <cube-button @click="showPicker">{{wo}}</cube-button>
    </div>
</template>

<script>

    export default {
        name: "lianxi",
        data () {
            return {
                times:'',
                wo:''
            }
        },
        methods: {
            showFormatPicker() {
                if (!this.Picker) {
                    this.Picker = this.$createDatePicker({
                        title: 'Use format',
                        min: new Date(2018, 7, 8),
                        max: new Date(2020, 9, 20),
                        value: new Date(),
                        format: {
                            year: 'YYYY',
                            month: 'MM',
                            date: 'D'
                        },
                        onSelect: this.selectHandle,
                        onCancel: this.cancelHandle
                    })
                }

                this.Picker.show()
            },
            selectHandle(date, selectedVal, selected) {
                console.log(1);
                this.times = `${selected.join('-')}`;
            },
            cancelHandle() {
            },


            showPicker() {
                if (!this.formatPicker) {
                    this.formatPicker = this.$createDatePicker({
                        title: 'Use format',
                        min: new Date(2008, 7, 8),
                        max: new Date(2020, 9, 20),
                        value: new Date(),
                        format: {
                            year: 'YY',
                            month: 'MM',
                            date: ' D '
                        },
                        onSelect: this.user,
                    })
                }

                this.formatPicker.show()
            },
            user(date, selectedVal, selectedText) {
                console.log(2);
                this.wo = `${selectedText.join('-')}`;
            },
        },
        computed: {

        }
    }
</script>

<style scoped>
.times{
    font-size: 0.24rem;
}
</style>